import { I_Scene } from '@containers/Home/types';
import Flickity from 'react-flickity-component';
import 'flickity/css/flickity.css';

interface I_ListSceneProps {
  sceneSelected?: I_Scene;
  scenesGroupActive?: I_Scene[];
  onSetRef: (c) => void;
}

export function ListSceneFlickity({
  onSetRef,
  scenesGroupActive,
  sceneSelected,
}: I_ListSceneProps) {
  return (
    <Flickity
      elementType="div"
      className="carousel"
      flickityRef={c => {
        if (c) onSetRef(c);
      }}
      options={{
        freeScroll: true,
        freeScrollFriction: 0.03,
        contain: true,
        prevNextButtons: false,
        pageDots: false,
        lazyLoad: true,
        cellAlign: 'left',
      }}
    >
      {scenesGroupActive?.map(scene => (
        <div
          key={scene.id}
          className={`${
            sceneSelected?.id === scene.id ? 'active' : ''
          } itemScene`}
        >
          <img src={scene.screenshot || scene.media?.src_thumb || ''} alt="" />
          <h2 className="--sceneTitle">{scene.title}</h2>
        </div>
      ))}
    </Flickity>
  );
}

export default ListSceneFlickity;
